<script>
// import bgc from '@/assets/sss9.jpg';
export default {
  data() {
    return {
      drawer: false,
      direction: 'rtl',
      images: [],
      imagesOne: [],
      fetchInterval: null,
      timeoutDuration: 5000, // 超时5秒
    };
  },
  methods: {
    heisiGirl() {
      this.images = [];
      this.startFetchingOne();
    },
    baisiGirl() {
      this.images = [];
      this.startFetchingTwo();
    },
    qcGirl() {
      this.images = [];
      this.startFetching();
    },
    getValidImage(index) {
      if (this.images[index] && this.images[index].length > 0) {
        return this.images[index]; // 返回有效的图片
      } else {
        return this.images[14] || '@assets/image1.png'; // 返回第一个有效图片或默认图片
      }
    },

    expandBox() {
      // console.log('鼠标悬停事件触发');
    },
    collapseBox() {
      // console.log('鼠标移开事件触发');
    },

    async fetchImages() {
      try {
        const res = await this.$axios.get('/api/ksxjj.php?type=json');
        if (this.images.length < 15) {
          this.images.push(res.data.img);
        } // 将每次请求到的图片 URL 添加到数组中
      } catch (error) {
        console.error('请求出错:', error);
      }
    },
    startFetching() {
      this.fetchInterval = setInterval(async () => {
        await this.fetchImages();
        if (this.images.length >= 15) {
          clearInterval(this.fetchInterval);
          console.log('获取图片成功，停止请求');
        }
      }, 1000); // 每1秒检查一次
    },
    async fetchImagesOne() {
      try {
        const res = await this.$axios.get('/api/heisi.php?type=json');
        if (this.images.length < 15) {
          this.images.push(res.data.img);
        } // 将每次请求到的图片 URL 添加到数组中
      } catch (error) {
        console.error('请求出错:', error);
      }
    },
    startFetchingOne() {
      this.fetchInterval = setInterval(async () => {
        await this.fetchImagesOne();
        if (this.images.length >= 15) {
          clearInterval(this.fetchInterval);
          console.log('获取图片成功，停止请求');
        }
      }, 1000); // 每1秒检查一次
    },
    async fetchImagesTwo() {
      try {
        const res = await this.$axios.get('/api/baisi.php?type=json');
        if (this.images.length < 15) {
          this.images.push(res.data.img);
        } // 将每次请求到的图片 URL 添加到数组中
      } catch (error) {
        console.error('请求出错:', error);
      }
    },
    startFetchingTwo() {
      this.fetchInterval = setInterval(async () => {
        await this.fetchImagesTwo();
        if (this.images.length >= 15) {
          clearInterval(this.fetchInterval);
          console.log('获取图片成功，停止请求');
        }
      }, 1000); // 每1秒检查一次
    },
  },
  mounted() {
    this.startFetching();
  },
};
